.wide_column_container {
  max-width: 1820px;
  margin: 0 auto;
  padding: 30px 0;
}

.card_list{
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 10px;
}

.card_layout {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 30px 10px;
}

.card {
  width: 207px;
  height: 344px;
  background-color: white;
  box-shadow: rgba(0, 0, 0, 0.3) 0px 2px 20px 0px;	
  position: relative;
}

.card_top{
  position: relative;
  height: 269px;
  padding: 12px;
  background-color: #16a085;
}

.card_top > div{
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.card_icon {
  width: 18px;
  height: 18px;
}

.card h3 {
  font-size: 18px;
  color: white;
  word-break: keep-all;
  text-align: left;
  padding: 10px 0;
}

.card h4 {
  font-size: 18px;
  color: white;
  word-break: keep-all;
  text-align: left;
}

.card p {
  font-size: 12px;
  margin: 12px;
  padding: 0 2px;
  color: #e0e0e0;
  word-break: keep-all;
  text-align: right;
  display: block;
  position: absolute;
  right: 0;
  bottom: 0;
}

button.card_circle {
  padding: 0;
  margin: 0;
  width: 27px;
  height: 27px;
  border-radius: 100%;
  background-color: #e0e0e0;
}

button.card_rect {
  position: absolute;
  right: 0;
  bottom: 0;
  padding: 0;
  margin: 12px;
  width: 89px;
  height: 25px;
  font-size: 11px;
  border-radius: 0;
  background-color: #16a085;
}
